<template>
  <button class="btn btn-light btn-sm pb-0 float-right mr-2" @click="selectOption">
    <i class="fa fa-plus" v-if="!isAll"></i>
    <i class="fa fa-remove" v-else></i>
  </button>
</template>

<script>
import tool from '@/common/es/tool'
export default {
  name: 'SelectOption',
  props: {
    _listFull: {
      type: Array
    },
    _list: {
      type: Array
    }
  },
  data() {
    return {
      isAll: false
    }
  },
  mounted() {
    if (this._listFull.length == this._list.length) {
      this.isAll = true
    }
  },
  methods: {
    selectOption() {
      return 
      if (!this.isAll) {
        const none = this._listFull.filter((e) => {
          return !tool.inList(e.id, this._list)
        })
        
        for (const i of none) {
          this._list.push(i.id)
        }
        this.isAll = true

        tool.showToast('全选')
      } else {
        this._list.splice(1, (this._list.length - 1))
        this.isAll = false

        tool.showToast('移除全选')
      }
    } 
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/listing.sass"

</style>


