<template>
  <button class="btn btn-light btn-sm pb-0 float-right" @click="sortOption">

    <i class="fa fa-unsorted " :class="{ 'text-main': this.isOrder }"></i>

  </button>
</template>

<script>
import tool from '@/common/es/tool'

export default {
  name: 'SortOption',
  props: {
    _list: {
      type: Array
    }
  },
  data() {
    return {
      isOrder: false
    }
  },
  methods: {
    numOrder() {
      this._list.reverse()
      this.isOrder = !this.isOrder
      if (this.isOrder) { tool.showToast('逆序排列') } else { tool.showToast('顺序排列') }
    },
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/listing.sass"

.ordering-icon-pos-fix
  margin-bottom: 0px !important
  height: 18px !important
  display: inline-block
</style>


